import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { useContext } from 'react';
import styled from 'styled-components';
import { Helmet, HelmetProvider } from 'react-helmet-async'

const Viewer = (
    props: {
        id: string
    }
) => {
    const { state, dispatcher } = useContext(GlobalContext);
    return (
        <_Wrap>
            <HelmetProvider>
                <Helmet
                    title="VH Viewer"
                    meta={[
                        // { name: 'twitter:card', content: 'summary' },
                        // { name: 'twitter:title', content: 'Home' },
                        // { name: 'twitter:description', content: 'description of Home' },
                        // { name: 'twitter:image', content: 'http://path/to/image' },
                        { property: 'og:title', content: 'VH Viewer' },
                        { property: 'og:type', content: 'website' },
                        { property: 'og:url', content: 'http://path/to/this/url' },
                        { property: 'og:image', content: 'https://www.uxpin.com/studio/wp-content/uploads/2020/12/The-Best-React-Design-Patterns-You-Should-Know-About-1-1024x512.png' },
                        { property: 'og:description', content: `Viewer Title = ${props.id}` },
                    ]}
                />
            </HelmetProvider>
            <_Confirm>{`ID=${props.id}`}</_Confirm>
        </_Wrap>
    );
}

export default Viewer;


const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #9e1717;
`;

const _Confirm = styled.div`
    display: inline-block;
    font-size: 40px;
    font-weight: 600;
    color: white;
`