import { BEAT_WIDTH, KEY12_SHARP_LIST, SymbolKey, SymbolParams, SymbolType, SYMBOL_PARAMS_LIST, TimeSignatures } from "contents/common/define";
import { applyOnChord, getSymbolParams, loadSampler } from "contents/common/utils";
import { ActionType } from "./reducer";
import { Action, BackingLen, BackingEditorState, BackingLayer, BACKING_LEN_TYPE, BACKING_SIDE, ChannelParam, DetailInit, NoteData, Store, TypeNoteElement, NoteState, NoteElement, FloatWndState, DetailChord, BackingScript, DetailSection, EditMode, ScoreCache, DetailChange, DetailFixed, LoginParams, ChordPaletteState, ChangeType, ModulateRelation, Key12Info, TempoRelation, ThemaIndexedElement, ThemaDataTree, ThemaState, BackingPattern, BackingVoicing, BTPattern, BTVoicing, BTSustain, VoicingTargetType, VoicingTargetCondition, Dialog, Column, SoundFontProp } from "./store";
import Soundfont from 'soundfont-player';
import { FormDispatcher } from "./store/FormDispatcher";
import { TuneState } from "./store/TuneState";

/**
 * ビジネスロジックからステートを更新するためのクラス。
 *
 * @export
 * @class Dispatcher
 */
export class Dispatcher {

    private dispatchBase: React.Dispatch<Action>;
    private dispatch: Function;
    private state: Store;

    /**
     *Creates an instance of Dispatcher.
     * @param {Store} state
     * @param {React.Dispatch<Action>} dispatchBase
     * @memberof Dispatcher
     */
    constructor(state: Store, dispatchBase: React.Dispatch<Action>) {
        this.dispatchBase = dispatchBase;
        this.state = state;
        this.dispatch = (type: ActionType, payload: any) => dispatchBase({ type, payload });
    }

    public setLoginParams = (login: LoginParams) => {
        this.dispatch(ActionType.SET_LOGIN, { login });
    }

    public callDialog = (left: number, top: number, contents: JSX.Element, arg?: object) => {
        const dialog: Dialog = { left, top, contents, arg };
        this.dispatch(ActionType.SET_DIALOG, { dialog });
    }

    public closeDialog = () => {
        this.dispatch(ActionType.SET_DIALOG, { dialog: { ...this.state.dialog, contents: null } });
    }

    public setDinamicCache = (dinamicCache: null | object) => {
        this.dispatch(ActionType.DINAMIC_CACHE, { dinamicCache });
    }
    public setFormDispatcher = (formDispatcher: FormDispatcher) => {
        this.dispatch(ActionType.SET_FORM_DISPATCHER, { formDispatcher });
    }

    /**
     * 
     * @param noteData 
     */
    public setNoteData = (data: null | NoteData) => {
        this.dispatchBase({ type: ActionType.SET_NOTE_DATA, payload: { data } });
    }
    public initBackingPalette = (backingPaletteState: BackingEditorState) => {
        this.dispatchBase({ type: ActionType.INIT_BACKING_PALETTE, payload: { backingPaletteState } });
    }

    public setUseChannels = (useChannels: ChannelParam[]) => {
        this.dispatchBase({ type: ActionType.SET_USE_CHANNELS, payload: { useChannels } });
    }

    public setSampler = (sample: object) => {
        const sampler = loadSampler(sample);
        this.dispatchBase({ type: ActionType.SET_SAMPLER, payload: { sampler } });
    }
    public setInstruments = (instrument: null | Soundfont.Player) => {
        this.dispatchBase({ type: ActionType.SET_INSTRUMENT, payload: { instrument } });
    }

    public score = {

        apllyFlasah: (index: number) => {
            const reffer = this.state.noteState.cacheList[index].reffer as HTMLDivElement;
            reffer.style.backgroundColor = '#ff0';
            setTimeout(() => {
                reffer.style.backgroundColor = '';
                // reffer.style.width = 'inherit';
            }, 350);
        },
        apllyFlashCur: () => {
            const focusIndex = this.state.noteState.focusIndex;
            this.score.apllyFlasah(focusIndex);
        },
        apllyFlashList: () => {
            this.state.noteState.selectedList.forEach((index) => {
                this.score.apllyFlasah(index);
            });
        },

        setNoteState: (noteState: NoteState) => {
            this.dispatch(ActionType.SET_NOTE_STATE, { noteState });
        },
        setCriteria: () => {
            const focusIndex = this.state.noteState.focusIndex;
            this.score.setNoteState({
                ...this.state.noteState,
                criteriaIndex: focusIndex, editMode: EditMode.TRANSPLANT
            });
        },
        setCloneCriteria: () => {
            const focusIndex = this.state.noteState.focusIndex;
            this.score.setNoteState({
                ...this.state.noteState,
                cloneRange: [focusIndex, focusIndex], editMode: EditMode.CLONE, cloneDataList: []
            });
        },
        setCloneRange: (index: number) => {
            const cloneRange = this.state.noteState.cloneRange;
            cloneRange[1] = index;
            this.score.setNoteState({
                ...this.state.noteState,
                cloneRange
            });
        },
        insertCloneData: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList;
            const cloneDataList = this.state.noteState.cloneDataList;
            const cacheList = this.state.noteState.cacheList.slice();
            cloneDataList.forEach((cloneData, i) => {
                dataList.splice(focusIndex + 1 + i, 0, cloneData);
                // cacheList.splice(focusIndex + 1, i, {} as ScoreCache);
            });
            this.score.setNoteData({
                ...this.state.noteData,
                dataList
            });
            // for(let i = focusIndex + 1; i < focusIndex + 1 + cloneDataList.length; i ++) {
            //     this.score.apllyFlasah(i);
            // }
            this.score.setNoteState({
                ...this.state.noteState,
                cloneDataList: []
            });
        },
        releaseCriteria: () => {
            this.score.setNoteState({
                ...this.state.noteState,
                criteriaIndex: -1, editMode: EditMode.NORMAL, selectedList: []
            });
        },
        releaseCloneCriteria: () => {
            this.score.setNoteState({
                ...this.state.noteState,
                cloneRange: [], editMode: EditMode.NORMAL
            });
        },
        setNoteData: (data: NoteData) => {
            this.dispatch(ActionType.SET_NOTE_DATA, { data });
        },
        /**
         * 初期値の要素を返す。
         */
        getInitElement: (): DetailInit => {
            return this.state.noteData.dataList[0].detail as DetailInit;
        },
        setEditMode: (editMode: EditMode) => {
            this.score.setNoteState({ ...this.state.noteState, editMode });
        },

        addNoteElement: (type: TypeNoteElement, detail: DetailSection | DetailChord | DetailChange) => {
            const curIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList.slice();
            dataList.splice(curIndex + 1, 0, { type, detail });
            this.dispatch(ActionType.SET_DATA_LIST, { dataList });
            const cacheList = this.state.noteState.cacheList.slice();
            cacheList.splice(curIndex + 1, 0, {} as ScoreCache);
            this.score.apllyFlasah(curIndex + 1);
        },

        addSectionElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList.slice();
            const detail = {} as DetailSection;
            detail.labelName = 'Section';
            dataList.splice(curIndex + 1, 0, {
                type: TypeNoteElement.Section,
                detail: detail
            });
            this.dispatch(ActionType.SET_DATA_LIST, { dataList });
            const cacheList = this.state.noteState.cacheList.slice();
            cacheList.splice(curIndex + 1, 0, {} as ScoreCache);
            this.score.apllyFlasah(curIndex + 1);
        },
        addChangeElement: () => {
            // const detail = { type: ChangeType.NONE } as DetailChange;
            // const mod:ChangeModulation = {
            //     relation: ModulateRelation.DOMINANT,
            //     value: 0,
            // }
            const detail = {
                changeType: null,
                modulateRelation: null,
                domminantValue: 0,
                keyAdjust: 0,
                tempoRelation: null,
                tempoDiff: 0,
                tempoRate: 0,
                tempoAbsolute: 0,
                beat: null
            } as DetailChange;
            this.score.addNoteElement(TypeNoteElement.Change, detail);
        },
        addChordElement: () => {
            const noteState = this.state.noteState;
            const curIndex = noteState.focusIndex;
            const dataList = this.state.noteData.dataList.slice();
            dataList.splice(curIndex + 1, 0, {
                type: TypeNoteElement.Chord,
                detail: {
                    // degreeIndex: -1,
                    // isFlat: false,
                    molecule: null,
                    denominator: null,
                    symbolKey: SymbolKey.MAJOR,
                    sustain: noteState.defaultSustain,
                    headAdjust: 0,
                    tailAdjust: 0,
                    backing: {
                        useChannels: [],
                        layers: [
                            {
                                lens: [{
                                    len: BACKING_LEN_TYPE.LEN4,
                                    isDiv3: false,
                                    isHalf: false
                                }],
                                side: BACKING_SIDE.LEFT,
                                table: [[[false]]],
                                vels: [5]
                            },
                            {
                                lens: [{
                                    len: BACKING_LEN_TYPE.LEN4,
                                    isDiv3: false,
                                    isHalf: false
                                }],
                                side: BACKING_SIDE.RIGHT,
                                table: [[[false]]],
                                vels: [5]
                            }
                        ],
                        isUsePedal: false
                    }
                }
            });
            this.dispatch(ActionType.SET_DATA_LIST, { dataList });

            const cacheList = this.state.noteState.cacheList.slice();
            cacheList.splice(curIndex + 1, 0, {} as ScoreCache);
            this.score.apllyFlasah(curIndex + 1);
        },
        deleteElement: (index: number) => {
            const curIndex = this.state.noteState.focusIndex;
            const curElement = this.state.noteData.dataList[index];
            if (
                // 対象の要素がコードorセクションである
                [TypeNoteElement.Chord, TypeNoteElement.Section, TypeNoteElement.Change].includes(curElement.type) &&
                // 先頭のセクションは削除できない
                index != 2
            ) {
                const dataList: NoteElement[] = this.state.noteData.dataList.slice();
                dataList.splice(index, 1);
                this.dispatch(ActionType.SET_DATA_LIST, { dataList });
                if (index <= curIndex) {
                    this.state.noteState.focusIndex--;
                    this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                }
            }
        },
        deleteCurElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            this.score.deleteElement(curIndex);
            // const box = this.state.noteData.dataList[curIndex];
            // const ref = box.reffer as HTMLDivElement;
            // // ref.style.width = '0';
            // ref.style.opacity = '0';
            // setTimeout(() => {
            //     this.score.deleteElement(curIndex);
            // }, 1);
        },
        focusPreviousElement: (): boolean => {
            const curIndex = this.state.noteState.focusIndex;
            const prev = this.state.noteData.dataList[curIndex - 1];
            if (prev.type != TypeNoteElement.Fixed) {
                this.state.noteState.focusIndex--;
                this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                return true;
            }
            return false;
        },

        focusNextElement: (): boolean => {
            const curIndex = this.state.noteState.focusIndex;
            const next = this.state.noteData.dataList[curIndex + 1];
            if (next.type != TypeNoteElement.Fixed) {
                // this.state.noteState.focusIndex++;
                const focusIndex = curIndex + 1;
                // this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                this.score.setNoteState({ ...this.state.noteState, focusIndex });
                return true;
            }
            return false;
        },

        focusPreviousSection: () => {
            const dataList = this.state.noteData.dataList;
            let curIndex = this.state.noteState.focusIndex;
            do {
                curIndex--;
                if (dataList[curIndex].type == TypeNoteElement.Section) {
                    this.state.noteState.focusIndex = curIndex;
                    this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                    break;
                }
            } while (curIndex > 0);
        },

        focusNextSection: () => {
            const dataList = this.state.noteData.dataList;
            let curIndex = this.state.noteState.focusIndex;
            do {
                curIndex++;
                if (dataList[curIndex].type == TypeNoteElement.Section || curIndex == dataList.length - 2) {
                    this.state.noteState.focusIndex = curIndex;
                    this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                    break;
                }
            } while (curIndex < dataList.length - 1);
        },

        focusInitElement: () => {
            this.state.noteState.focusIndex = 0;
            this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
        },

        setSelectedList: (selectedList: number[]) => {
            this.dispatch(ActionType.SET_NOTE_STATE, {
                noteState: { ...this.state.noteState, selectedList }
            });
        },

        callChordSelector: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            // let degreeIndex = detail.degreeIndex;
            // if (degreeIndex == -1) {
            //     degreeIndex = 0;
            // }
            let molecule = detail.molecule;
            if (molecule == null) {
                molecule = { rootIndex: 0, isFlat: false };
            }
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
            this.dispatch(ActionType.CHORD_PALETTE_SETTER, {
                params: {
                    ...this.state.noteState.chordPaletteState,
                    // rootIndex: degreeIndex,
                    // isFlat: detail.isFlat,
                    molecule,
                    denominator: detail.denominator,
                    symbolKey: detail.symbolKey,
                    sustain: detail.sustain,
                    headAdjust: detail.headAdjust,
                    tailAdjust: detail.tailAdjust,
                } as ChordPaletteState
            });
        },

        callSectionEditor: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailSection;
            this.setDinamicCache(detail);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.SECTION_PALETTE });
        },

        callChangeEditor: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChange;
            this.setDinamicCache(detail);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHANGE_PALETTE });
        },

        callBackingEditor: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const init = this.state.noteState.cacheList[focusIndex].init;
            const backing = detail.backing;
            const molecule = detail.molecule as Key12Info;
            const backingState: BackingEditorState = {
                target: "note",
                isEditPattern: true,
                isDispVoicing: true,
                molecule: { rootIndex: molecule.rootIndex + init.keyIndex, isFlat: molecule.isFlat },
                denominator: detail.denominator,
                symbolKey: detail.symbolKey,
                sustain: detail.sustain,
                headAdjust: detail.headAdjust,
                tailAdjust: detail.tailAdjust,
                channelRowSize: backing.layers[0].table.length,
                useChannels: JSON.parse(JSON.stringify(backing.useChannels)),
                isUsePedal: backing.isUsePedal,
                sideIndex: 0,
                selectedLenIndex: 0,
                selectedChannelIndex: -1,
                layers: JSON.parse(JSON.stringify(backing.layers)),
                isCorrect: true
            };
            this.initBackingPalette(backingState);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.BACKING_PALETTE });
        },

        callThemaAccessor: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            this.setDinamicCache(detail);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.THEMA_ACCESSOR });
        }
    }

    /**
     * 初期値エディタ画面の操作に関連するメソッドを定義するオブジェクト。
     *
     * @memberof Dispatcher
     */
    public init = {
        setInitParam: (tempo: number, rythm: string, keyIndex: number, scale: string) => {
            const focusIndex = this.state.noteState.focusIndex;
            // const detail = this.state.noteData.dataList[focusIndex].detail as DetailInit;
            // const editorState = this.state.noteState.chordPaletteState;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    tempo, rythm, keyIndex, scale
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            this.score.apllyFlashCur();
        }
    }

    /**
     * セクションエディタ画面の操作に関連するメソッドを定義するオブジェクト。
     *
     * @memberof Dispatcher
     */
    public section = {
        setSectionParam: (labelName: string) => {
            const focusIndex = this.state.noteState.focusIndex;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    labelName
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            this.score.apllyFlashCur();
        }
    }

    /**
     * チェンジエディタ画面の操作に関連するメソッドを定義するオブジェクト。
     *
     * @memberof Dispatcher
     */
    public change = {
        applyChangeParams: (changeType: ChangeType, modulateRelation: null | ModulateRelation, domminantValue: number, keyAdjust: number,
            tempoRelation: null | TempoRelation, tempoDiff: number, tempoRate: number, tempoAbsolute: number, beat: null | TimeSignatures) => {
            const focusIndex = this.state.noteState.focusIndex;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    changeType,
                    modulateRelation,
                    domminantValue,
                    keyAdjust,
                    tempoRelation,
                    tempoDiff,
                    tempoRate,
                    tempoAbsolute,
                    beat
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            this.score.apllyFlashCur();
        }
    }

    /**
     * コードエディタ画面の操作に関連するメソッドを定義するオブジェクト。
     *
     * @memberof Dispatcher
     */
    public chord = {
        setCurChordParam: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const chordDetail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const editorState = this.state.noteState.chordPaletteState;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    ...chordDetail,
                    // degreeIndex: editorState.rootIndex,
                    // isFlat: editorState.isFlat,
                    molecule: editorState.molecule,
                    denominator: editorState.denominator,
                    symbolKey: editorState.symbolKey,
                    sustain: editorState.sustain,
                    headAdjust: editorState.headAdjust,
                    tailAdjust: editorState.tailAdjust,
                } as DetailChord
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            this.score.apllyFlashCur();
            // const reffer = this.state.noteData.dataList[focusIndex].reffer as HTMLDivElement;
            // reffer.style.backgroundColor = '#ff0';
            // setTimeout(() => {
            //     reffer.style.backgroundColor = '';
            // }, 350);
        }
    }

    /**
     * バッキングエディタ画面の操作に関連するメソッドを定義するオブジェクト。
     *
     * @memberof Dispatcher
     */
    public backing = {
        setPaletteState: (paletteState: BackingEditorState) => {
            const backing = this.state.noteState.backingPaletteState;
            this.score.setNoteState({ ...this.state.noteState, backingPaletteState: backing });
        },
        toggleMatrix: (channelIndex: number, lenIndex: number, divIndex: number) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const sideIndex = backing.sideIndex;
            const isUse = !backing.layers[sideIndex].table[channelIndex][lenIndex][divIndex];
            this.dispatch(ActionType.BACKING_TOGGLE_MATRIX, { sideIndex, channelIndex, lenIndex, divIndex, isUse });
        },
        addLen: () => {
            // this.dispatch({ type: ActionType.ADD_LEN, payload: null });

            const backingPaletteState = this.state.noteState.backingPaletteState as BackingEditorState;
            const script = backingPaletteState.layers;
            const sideIndex = backingPaletteState.sideIndex;
            const curSideScript = script[sideIndex];
            const selectedLenIndex = backingPaletteState.selectedLenIndex;
            const table = curSideScript.table;
            curSideScript.lens = [...curSideScript.lens];
            // 選択中の要素の次に挿入する
            curSideScript.lens.splice(
                selectedLenIndex + 1, 0,
                { len: BACKING_LEN_TYPE.LEN4, isHalf: false, isDiv3: false }
            );
            table.forEach((line) => {
                line.splice(
                    selectedLenIndex + 1, 0, [false]
                );
            });
            curSideScript.vels.splice(
                backingPaletteState.selectedLenIndex + 1, 0, 5
            );
            script[sideIndex] = curSideScript;

            this.dispatch(ActionType.INIT_BACKING_PALETTE, { backingPaletteState });
        },
        delLen: () => {
            // this.dispatch({ type: ActionType.ADD_LEN, payload: null });

            const backingPaletteState = this.state.noteState.backingPaletteState as BackingEditorState;
            const script = backingPaletteState.layers;
            const sideIndex = backingPaletteState.sideIndex;
            const curSideScript = script[sideIndex];
            const selectedLenIndex = backingPaletteState.selectedLenIndex;
            const table = curSideScript.table;
            curSideScript.lens = [...curSideScript.lens];
            // 選択中の要素を削除する
            curSideScript.lens.splice(selectedLenIndex, 1);
            table.forEach((line) => {
                line.splice(selectedLenIndex, 1);
            });
            curSideScript.vels.splice(selectedLenIndex, 1);
            script[sideIndex] = curSideScript;

            if (selectedLenIndex > 0) {
                backingPaletteState.selectedLenIndex--;
            }
            this.dispatch(ActionType.INIT_BACKING_PALETTE, { backingPaletteState });
        },
        updateLenIndex: (index: number) => {
            const backingPaletteState = this.state.noteState.backingPaletteState as BackingEditorState;
            backingPaletteState.selectedLenIndex = index;
            this.initBackingPalette(backingPaletteState);
        },
        getCurLenIndex: (): number => {
            const backingPaletteState = this.state.noteState.backingPaletteState as BackingEditorState;
            return backingPaletteState.selectedLenIndex;
        },
        setChannelRowSize: (size: number) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            backing.channelRowSize = size;
            backing.layers.forEach((layer) => {
                const table: boolean[][][] = new Array<boolean[][]>(size);
                console.log('★★★' + table.length);
                for (let i = 0; i < table.length; i++) {
                    table[i] = new Array<boolean[]>(layer.lens.length);
                    console.log('☆☆☆☆☆☆');
                    for (let j = 0; j < table[i].length; j++) {
                        table[i][j] = new Array<boolean>(1);
                        table[i][j][0] = false;
                    }
                }
                layer.table = table;
            });
            this.score.setNoteState({ ...this.state.noteState, backingPaletteState: backing });
        },
        isFirstCurLen: (): boolean => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            return backing.selectedLenIndex == 0;
        },
        isLastCurLen: (): boolean => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const sideScript = backing.layers[backing.sideIndex];
            return backing.selectedLenIndex == sideScript.lens.length - 1;
        },
        getSideScript: (): BackingLayer => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            return backing.layers[backing.sideIndex];
        },
        getCurLenBean: (): BackingLen => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const sideScript = backing.layers[backing.sideIndex];
            return sideScript.lens[backing.selectedLenIndex];
        },

        /**
         * 
         * @param lenBean 
         */
        setCurrentLenBean: (lenBean: BackingLen) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const script = backing.layers;
            const sideScript = script[backing.sideIndex];
            sideScript.lens[backing.selectedLenIndex] = lenBean;
            this.initBackingPalette({ ...backing, layers: script });
        },

        /**
         * 選択中の列の長さを設定します。
         * @param lenType 長さ
         */
        setCurLen: (lenType: BACKING_LEN_TYPE) => {
            const curLenBean = this.backing.getCurLenBean();
            curLenBean.len = lenType;
            curLenBean.isHalf = false;
            curLenBean.isDiv3 = false;
            this.backing.setCurrentLenBean(curLenBean);
        },

        /**
         * 選択中の列のベロシティ値を更新する。
         * @param velocity 更新するベロシティ値（1～10）
         */
        updateCurVolume: (velocity: number) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const script = backing.layers;
            script[backing.sideIndex].vels[backing.selectedLenIndex] = velocity;
            this.initBackingPalette({ ...backing, layers: script });
        },

        /**
         * 選択中の列のベロシティ値を返す。
         * @returns 選択中の列のベロシティ値
         */
        getCurVelocity: (): number => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const sideScript = backing.layers[backing.sideIndex];
            return sideScript.vels[backing.selectedLenIndex];
        },

        /**
         * 付点を設定できるか否かを判定する。
         * @returns 付点を設定できるか否か
         */
        isAcceptHalf: (): boolean => {
            return [
                BACKING_LEN_TYPE.LEN2,
                BACKING_LEN_TYPE.LEN4,
                BACKING_LEN_TYPE.LEN8
            ].includes(this.backing.getCurLenBean().len);
        },

        /**
         * 3連符を設定できるか否かを判定する。
         * @returns 3連符を設定できるか否か
         */
        isAcceptDiv3: (): boolean => {
            return [
                BACKING_LEN_TYPE.LEN4,
                BACKING_LEN_TYPE.LEN8
            ].includes(this.backing.getCurLenBean().len);
        },

        setLayerIndex: (index: number) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            backing.sideIndex = index;
            backing.selectedLenIndex = 0;
            this.initBackingPalette(backing);
        },
        setUsePedal: (use: boolean) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            backing.isUsePedal = use;
            this.initBackingPalette(backing);
        },
        setCorrect: (isCorrect: boolean) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            backing.isCorrect = isCorrect;
            this.initBackingPalette(backing);
        },

        changeLayerIndex: () => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            backing.sideIndex = backing.sideIndex == 0 ? 1 : 0;
            backing.selectedLenIndex = 0;
            this.initBackingPalette(backing);
        },

        setUseChannels: (useChannels: ChannelParam) => {
            const backing = this.state.noteState.backingPaletteState as BackingEditorState;
            const baseLayers = backing.layers;
            const layers: BackingLayer[] = new Array(2);
            for (let i = 0; i < 2; i++) {
                const channelCount = backing.useChannels.length;
                const lensSplitCount = baseLayers[i].lens.length;
                const baseTable = baseLayers[i].table;
                const table: boolean[][][] = new Array(channelCount);

                for (let y = 0; y < channelCount; y++) {
                    if (y < baseTable.length) {
                        table[y] = baseTable[y];
                    } else {
                        table[y] = new Array(lensSplitCount);
                        for (let x = 0; x < lensSplitCount; x++) {
                            table[y][x] = new Array(1);
                            table[y][x][0] = false;
                        }
                    }
                }
                layers[i] = {
                    ...baseLayers[i],
                    table,
                };
            }
        },

        setCurBackingScript: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const chordDetail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const editorState = this.state.noteState.backingPaletteState as BackingEditorState;
            const backing = {} as BackingScript;
            backing.useChannels = editorState.useChannels;
            backing.layers = editorState.layers;
            backing.isUsePedal = editorState.isUsePedal;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    ...chordDetail,
                    backing
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            this.score.apllyFlashCur();
        },

        getNotesCount: (backing: BackingScript): number => {

            let count = 0;
            backing.layers.forEach((layer) => {
                layer.table.forEach((line) => {
                    line.forEach((cell) => {
                        if (cell[0]) {
                            count++;
                        }
                    })
                })
            })
            return count;
        },

        play: (isScore: boolean) => {
            const curTime = new Date().getTime();

            const threads = this.state.noteState.backingThreads;
            const sampler = this.state.sampler;
            const soundUseList: number[] = Array(88);
            for (let i = 0; i < 88; i++) {
                soundUseList[i] = 0;
            }

            this.score.setNoteState({
                ...this.state.noteState,
                editMode: EditMode.PREVIEW, backingThreads: threads
            });
            let totalTime = 0;
            const focusIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList;
            // const curDetail  = dataList[focusIndex].detail as DetailChord;
            const pastTime = this.state.noteState.cacheList[focusIndex].progress.headMSec;

            // Audioファイルが指定されている場合のみ再生
            // バッキングエディタでは鳴らさない
            if (isScore && this.state.noteState.ensembleInfo.isLoad) {
                const audioHead = this.state.noteState.ensembleInfo.startPosition;
                const audio = this.state.noteState.ensembleInfo.audio;
                const adjust = audioHead + pastTime;
                audio.currentTime = 0;
                if (adjust > 0) {
                    audio.currentTime = adjust * 0.001;
                    audio.play();
                } else {
                    setTimeout(() => {
                        audio.play();
                    }, (adjust * -1));
                }
            }
            const limitIndex = isScore ? dataList.length : focusIndex + 1;
            for (let i = focusIndex; i < limitIndex; i++) {
                const data = dataList[i];


                // フォーカスを更新
                threads.push(
                    setTimeout(() => {
                        if (isScore && [TypeNoteElement.Chord, TypeNoteElement.Fixed].includes(data.type)) {
                            // const hasNext = this.score.focusNextElement();
                            // if (!hasNext) {
                            //     this.score.setNoteState({ ...this.state.noteState, editMode: EditMode.NORMAL });
                            // }
                            const [editMode, next] =
                                data.type == TypeNoteElement.Fixed ?
                                    [EditMode.NORMAL, focusIndex] : [EditMode.PREVIEW, i];
                            this.score.setNoteState({ ...this.state.noteState, focusIndex: next, editMode });
                        }
                    }, totalTime)
                );
                if (data.type == TypeNoteElement.Chord) {
                    const detail = data.detail as DetailChord;

                    const cache = this.state.noteState.cacheList[i];
                    const symbol: SymbolParams = { ...(getSymbolParams(detail.symbolKey) as SymbolParams) };
                    if (detail.molecule != null && detail.denominator != null) {
                        applyOnChord(symbol, detail.molecule.rootIndex, detail.denominator.rootIndex);
                    }
                    const intervals = symbol?.intervals as number[];
                    const editor = this.state.noteState.backingPaletteState as BackingEditorState;
                    const backing: BackingScript = !isScore ? { layers: editor.layers, useChannels: editor.useChannels, isUsePedal: editor.isUsePedal }
                        : detail.backing;
                    const tempo = cache.init.tempo;
                    // 1小節の長さミリ秒
                    const barMs = 60000 / tempo * 4;
                    const boxMs = 60000 / tempo * (detail.sustain + detail.headAdjust + detail.tailAdjust);
                    // 次のコードと滑らかにつなぐための補正
                    const adjustOverlap = barMs / 32;

                    // バッキングエディタの場合は、1つの要素で止める
                    if (!isScore) {
                        threads.push(
                            setTimeout(() => {
                                this.score.setNoteState({ ...this.state.noteState, editMode: EditMode.NORMAL });

                            }, totalTime + boxMs)
                        )
                    }
                    for (let i = 0; i < 2; i++) {
                        let headMs = 0;
                        const layer = backing.layers[i];
                        // 音価のループ
                        layer.lens.forEach((lenBean, j) => {

                            const lenValue = Number(lenBean.len.toString().replace('LEN', ''));
                            let curLenMs = barMs / lenValue;
                            if (lenBean.isHalf) {
                                curLenMs *= 1.5;
                            }
                            backing.useChannels.forEach((channel, k) => {
                                layer.table[k][j].forEach((notes, l) => {

                                    if (notes && detail.molecule != null) {
                                        const degreeIndex = detail.molecule.rootIndex;
                                        const rootKeyIndex = (cache.init.keyIndex + degreeIndex) % 12;
                                        const soundIndex = rootKeyIndex + intervals[channel.structIndex] + channel.octave * 12;
                                        const octave = Math.floor(soundIndex / 12);
                                        const root = KEY12_SHARP_LIST[soundIndex % 12];
                                        const soundFullName = root + octave;
                                        const velocity = 0.5 * layer.vels[j] / 10;
                                        const head = headMs + curLenMs / 3 * l;
                                        const tail = !backing.isUsePedal ?
                                            //ペダル未使用時
                                            head + curLenMs :
                                            //ペダル使用時
                                            boxMs + adjustOverlap
                                            ;
                                        const lag = new Date().getTime() - curTime;
                                        threads.push(
                                            setTimeout(() => {
                                                sampler.triggerAttack(soundFullName, '+0', velocity);
                                                soundUseList[soundIndex]++;
                                            }, totalTime + head - lag)
                                        );
                                        threads.push(
                                            setTimeout(() => {
                                                soundUseList[soundIndex]--;
                                                if (soundUseList[soundIndex] == 0) {
                                                    sampler.triggerRelease(soundFullName);
                                                }
                                            }, totalTime + tail - lag)
                                        );
                                    }
                                });
                            });
                            headMs += curLenMs;
                        });
                    }
                    totalTime += boxMs;
                }
            }

            if (isScore) {
                const start = this.state.noteState.cacheList[focusIndex].progress.headMSec;
                this.backing.playTune(curTime, start);
            }
        },
        playTune: (curTime: number, start: number) => {
            const columnList = this.state.noteData.columnList;
            const threads = this.state.noteState.backingThreads;
            const getSoundName = (soundIndex: number) => {

                const octave = Math.floor((87 + 9 - soundIndex) / 12);
                const keyIndex = (87 + 9 - soundIndex) % 12;
                const chordName = KEY12_SHARP_LIST[keyIndex];
                return chordName + octave;
            }

            let pastTime = 140 - start;
            const bpm = this.state.noteState.cacheList[0].init.tempo;
            for (let i = 0; i < columnList.length; i++) {
                const column = columnList[i];

                const total = column.len4 + column.len8 / 2 + column.len16 / 4 + column.len4div3 / 3 + column.len8div3 / 6;
                const soundName = getSoundName(column.soundIndex);
                const time = 60 / bpm * total;
                if (pastTime > 0) {
                    const lag = new Date().getTime() - curTime;
                    if (column.soundIndex !== -1) {
                        threads.push(
                            setTimeout(() => {
                                this.tune.playSoundFont(soundName, time);
                            }, pastTime - lag)
                        );
                    }
                }
                pastTime += time * 1000;
            }
        }
    }

    public thema = {

        setThemaeData: (data: ThemaDataTree) => {
            this.dispatch(ActionType.SET_THEMA_DATA, { data: { ...data } });
        },
        setThemaState: (themaState: ThemaState) => {
            this.dispatch(ActionType.SET_THEMA_STATE, { themaState });
        },

        callBackingPatternEditor: () => {
            const cur = this.thema.getFocusElement();
            const sustainEl = cur.parent as ThemaIndexedElement;
            const sustain = sustainEl.reffer as BTSustain;
            const form = this.state.themaState.form.pattern;
            const patternDef: BackingPattern = form.src == '' ? {
                layers: [
                    {
                        lens: [{
                            len: BACKING_LEN_TYPE.LEN4,
                            isDiv3: false,
                            isHalf: false
                        }],
                        side: BACKING_SIDE.LEFT,
                        table: [[[false]]],
                        vels: [5]
                    },
                    {
                        lens: [{
                            len: BACKING_LEN_TYPE.LEN4,
                            isDiv3: false,
                            isHalf: false
                        }],
                        side: BACKING_SIDE.RIGHT,
                        table: [[[false]]],
                        vels: [5]
                    }
                ],
                isUsePedal: false
            } : JSON.parse(form.src);
            const backingState: BackingEditorState = {
                target: "thema-pattern",
                isDispVoicing: false,
                isEditPattern: true,
                molecule: { rootIndex: 0, isFlat: false },
                denominator: null,
                symbolKey: SymbolKey.MAJOR,
                sustain: sustain.beat,
                headAdjust: sustain.headAdjust,
                tailAdjust: sustain.tailAdjust,
                channelRowSize: patternDef.layers[0].table.length,
                isUsePedal: patternDef.isUsePedal,
                sideIndex: 0,
                selectedLenIndex: 0,
                selectedChannelIndex: -1,
                useChannels: [],
                layers: patternDef.layers.slice(),
                isCorrect: true
            };
            this.initBackingPalette(backingState);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.BACKING_PALETTE });
        },
        callBackingVoicingEditor: () => {
            const cur = this.thema.getFocusElement();
            const patternEl = cur.parent?.parent?.parent as ThemaIndexedElement;
            const sustainEl = patternEl.parent as ThemaIndexedElement;
            const pattern = patternEl.reffer as BTPattern;
            const sustain = sustainEl.reffer as BTSustain;
            const patternDef = pattern.def as BackingPattern;
            const form = this.state.themaState.form.voicing;
            const VoicingDef: BackingVoicing = form.src == '' ? {
                useChannels: []
            } : JSON.parse(form.src);
            const backingState: BackingEditorState = {
                target: "thema-voicing",
                isDispVoicing: true,
                isEditPattern: false,
                molecule: { rootIndex: 0, isFlat: false },
                denominator: null,
                symbolKey: SymbolKey.MAJOR,
                sustain: sustain.beat,
                headAdjust: sustain.headAdjust,
                tailAdjust: sustain.tailAdjust,
                channelRowSize: patternDef.layers[0].table.length,
                isUsePedal: patternDef.isUsePedal,
                sideIndex: 0,
                selectedLenIndex: 0,
                selectedChannelIndex: -1,
                useChannels: VoicingDef.useChannels.slice(),
                layers: patternDef.layers,
                isCorrect: true
            };
            this.initBackingPalette(backingState);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.BACKING_PALETTE });
        },

        buildIndex: () => {
            const themaState = this.state.themaState;
            const themaTree = this.state.themaDataTree;
            const dataIndexies: ThemaIndexedElement[] = [];

            const rootBean: ThemaIndexedElement = {
                isClose: false,
                ChildCnt: themaTree.sustains.length,
                type: 'root',
                reffer: themaTree,
                parent: null
            }
            dataIndexies.push(rootBean);

            // 拍リストを構成
            themaTree.sustains.forEach((sustain) => {
                const sustainBean: ThemaIndexedElement = {
                    isClose: false,
                    ChildCnt: sustain.patterns.length,
                    type: 'sustain',
                    reffer: sustain,
                    parent: rootBean
                };
                dataIndexies.push(sustainBean);
                sustain.patterns.forEach((pattern) => {
                    const patternBean: ThemaIndexedElement = {
                        isClose: false,
                        ChildCnt: pattern.tgtTypes.length,
                        type: 'pattern',
                        reffer: pattern,
                        parent: sustainBean
                    };
                    dataIndexies.push(patternBean);
                    pattern.tgtTypes.forEach((category) => {
                        const categoryBean: ThemaIndexedElement = {
                            isClose: false,
                            ChildCnt: category.conditions.length,
                            type: 'category',
                            reffer: category,
                            parent: patternBean
                        };
                        dataIndexies.push(categoryBean);
                        category.conditions.forEach((condition) => {
                            const conditionBean: ThemaIndexedElement = {
                                isClose: false,
                                ChildCnt: condition.voicings.length,
                                type: 'condition',
                                reffer: condition,
                                parent: categoryBean
                            };
                            dataIndexies.push(conditionBean);
                            condition.voicings.forEach((voicing) => {
                                const voicingBean: ThemaIndexedElement = {
                                    isClose: false,
                                    ChildCnt: 0,
                                    type: 'voicing',
                                    reffer: voicing,
                                    parent: conditionBean
                                };
                                dataIndexies.push(voicingBean);
                            });
                        });
                    });
                });
            });

            if (themaState.focusRef !== null) {
                dataIndexies.forEach((data, i) => {
                    if (data.reffer == themaState.focusRef) {
                        themaState.focusIndex = i;
                        themaState.focusRef = null;
                        themaState.isCreate = true;
                    }
                });
            }
            this.thema.setThemaState({ ...themaState, themaCacheList: dataIndexies });
        },
        applyBackingPattern: () => {
            const editor = this.state.noteState.backingPaletteState as BackingEditorState;
            const form = this.state.themaState.form.pattern;
            const def: BackingPattern = {
                layers: editor.layers.slice(),
                isUsePedal: editor.isUsePedal
            }
            form.src = JSON.stringify(def);
            this.thema.setThemaState(this.state.themaState);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
        },
        applyBackingVoicing: () => {
            const editor = this.state.noteState.backingPaletteState as BackingEditorState;
            const form = this.state.themaState.form.voicing;
            const def: BackingVoicing = {
                useChannels: editor.useChannels
            }
            form.src = JSON.stringify(def);
            this.thema.setThemaState(this.state.themaState);
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
        },

        getFocusElement: (): ThemaIndexedElement => {
            return this.state.themaState.themaCacheList[this.state.themaState.focusIndex];
        },

        addChildSustain: () => {
            const thema = this.state.themaDataTree;
            const themaState = this.state.themaState;
            const bean: BTSustain = {
                beat: 1,
                headAdjust: 0,
                tailAdjust: 0,
                patterns: []
            };
            themaState.focusRef = bean;
            const form = themaState.form.sustain;
            form.beat = null;
            form.headAdjust = 0;
            form.tailAdjust = 0;
            this.thema.setThemaState(themaState);
            thema.sustains.push(bean);
            this.thema.setThemaeData(thema);
        },

        addChildPattern: (data: ThemaIndexedElement) => {
            const thema = this.state.themaDataTree;
            const themaState = this.state.themaState;
            const reffer = data.reffer as BTSustain;
            const bean: BTPattern = {
                name: 'pattern1',
                def: null,
                tgtTypes: []
            };
            themaState.focusRef = bean;
            const form = themaState.form.pattern;
            form.name = '';
            form.def = null;
            form.src = '';
            this.thema.setThemaState(themaState);
            reffer.patterns.push(bean);
            this.thema.setThemaeData(thema);
        },

        addChildCategory: (data: ThemaIndexedElement) => {
            const thema = this.state.themaDataTree;
            const themaState = this.state.themaState;
            const reffer = data.reffer as BTPattern;
            const bean: VoicingTargetType = {
                symbolType: SymbolType.TRIAD,
                conditions: []
            };
            themaState.focusRef = bean;
            const form = themaState.form.category;
            form.category = null;
            this.thema.setThemaState(themaState);
            reffer.tgtTypes.push(bean);
            this.thema.setThemaeData(thema);
        },

        addChildCondition: (data: ThemaIndexedElement) => {
            const thema = this.state.themaDataTree;
            const themaState = this.state.themaState;
            const reffer = data.reffer as VoicingTargetType;
            const bean: VoicingTargetCondition = {
                maxRootIndex: 11,
                minRootIndex: 0,
                tgtSymbols: [],
                voicings: []
            };
            themaState.focusRef = bean;
            const form = themaState.form.condition;
            form.maxRootIndex = 0;
            form.maxRootIndex = 11;
            form.tgtSymbols = [];
            this.thema.setThemaState(themaState);
            reffer.conditions.push(bean);
            this.thema.setThemaeData(thema);
        },

        addChildVoicing: (data: ThemaIndexedElement) => {
            const thema = this.state.themaDataTree;
            const themaState = this.state.themaState;
            const reffer = data.reffer as VoicingTargetCondition;
            const bean: BTVoicing = {
                def: null
            };
            themaState.focusRef = bean;
            const form = themaState.form.voicing;
            form.def = null;
            form.src = '';
            this.thema.setThemaState(themaState);
            reffer.voicings.push(bean);
            this.thema.setThemaeData(thema);
        }
    }

    public preview = {
        play: () => {

            const threads = this.state.noteState.backingThreads;
            const soundUseList: number[] = Array(88);
            for (let i = 0; i < 88; i++) {
                soundUseList[i] = 0;
            }

            this.score.setNoteState({
                ...this.state.noteState,
                editMode: EditMode.PREVIEW, backingThreads: threads
            });
            let totalTime = 0;
            const focusIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList;
            const pastTime = 0;

            const audioHead = this.state.noteState.ensembleInfo.startPosition;

            // Audioファイルが指定されている場合のみ再生
            if (this.state.noteState.ensembleInfo.fileURL !== '') {
                const audio = this.state.noteState.ensembleInfo.audio;
                audio.currentTime = (audioHead + pastTime) * 0.001;
                audio.play();
            }
            const curTime = new Date().getTime();
            const limitIndex = dataList.length;
            for (let i = focusIndex; i < limitIndex; i++) {
                const data = dataList[i];

                if (data.type == TypeNoteElement.Chord) {
                    const detail = data.detail as DetailChord;

                    const cache = this.state.noteState.cacheList[i];
                    const symbol: SymbolParams = { ...(getSymbolParams(detail.symbolKey) as SymbolParams) };
                    if (detail.molecule != null && detail.denominator != null) {
                        applyOnChord(symbol, detail.molecule.rootIndex, detail.denominator.rootIndex);
                    }
                    const intervals = symbol?.intervals as number[];
                    const backing: BackingScript = detail.backing;
                    const sampler = this.state.sampler;
                    const tempo = cache.init.tempo;
                    // 1小節の長さミリ秒
                    const barMs = 60000 / tempo * 4;
                    const boxMs = 60000 / tempo * (detail.sustain + detail.headAdjust + detail.tailAdjust);
                    const adjustOverlap = barMs / 32;

                    for (let i = 0; i < 2; i++) {
                        let headMs = 0;
                        const layer = backing.layers[i];
                        // 音価のループ
                        layer.lens.forEach((lenBean, j) => {

                            const lenValue = Number(lenBean.len.toString().replace('LEN', ''));
                            let curLenMs = barMs / lenValue;
                            if (lenBean.isHalf) {
                                curLenMs *= 1.5;
                            }
                            backing.useChannels.forEach((channel, k) => {
                                layer.table[k][j].forEach((notes, l) => {

                                    if (notes && detail.molecule != null) {
                                        const degreeIndex = detail.molecule.rootIndex;
                                        const rootKeyIndex = (cache.init.keyIndex + degreeIndex) % 12;
                                        const soundIndex = rootKeyIndex + intervals[channel.structIndex] + channel.octave * 12;
                                        const octave = Math.floor(soundIndex / 12);
                                        const root = KEY12_SHARP_LIST[soundIndex % 12];
                                        const soundFullName = root + octave;
                                        const velocity = 0.5 * layer.vels[j] / 10;
                                        const head = headMs + curLenMs / 3 * l;
                                        const tail = !backing.isUsePedal ?
                                            //ペダル未使用時
                                            head + curLenMs :
                                            //ペダル使用時
                                            boxMs + adjustOverlap
                                            ;
                                        const lag = new Date().getTime() - curTime;
                                        threads.push(
                                            setTimeout(() => {
                                                sampler.triggerAttack(soundFullName, '+0', velocity);
                                                soundUseList[soundIndex]++;
                                            }, totalTime + head - lag)
                                        );
                                        threads.push(
                                            setTimeout(() => {
                                                soundUseList[soundIndex]--;
                                                if (soundUseList[soundIndex] == 0) {
                                                    sampler.triggerRelease(soundFullName);
                                                }
                                            }, totalTime + tail - lag)
                                        );
                                    }
                                });
                            });
                            headMs += curLenMs;
                        });
                    }
                    totalTime += boxMs;
                }
            }
        },
        stop: () => {

            // Audioファイルが指定されている場合のみ停止
            if (this.state.noteState.ensembleInfo.isLoad) {
                this.state.noteState.ensembleInfo.audio.pause();
            }
            const threads = this.state.noteState.backingThreads;
            threads.forEach((thr) => {
                clearInterval(thr);
            });
            this.score.setNoteState({
                ...this.state.noteState,
                backingThreads: [], editMode: EditMode.NORMAL
            });
            this.state.sampler.releaseAll();
        }
    }

    public tune = {
        setState: (tuneState: TuneState) => {
            this.dispatchBase({ type: ActionType.SET_TUNE_STATE, payload: { tuneState } });
        },
        setSoundFont: (soundFont: SoundFontProp) => {
            this.setNoteData({ ...this.state.noteData, soundFont: soundFont });
        },

        setColumnList: (columnList: Column[]) => {
            this.setNoteData({ ...this.state.noteData, columnList });
        },

        addColumn: () => {
            const tuneState = this.state.tuneState;
            const len4 = tuneState.usingLen === 4 && !tuneState.isDiv3 ? 1 : 0;
            const len8 = tuneState.usingLen === 8 && !tuneState.isDiv3 ? 1 : 0;
            const len16 = tuneState.usingLen === 16 ? 1 : 0;
            const len4div3 = tuneState.usingLen === 4 && tuneState.isDiv3 ? 1 : 0;
            const len8div3 = tuneState.usingLen === 8 && tuneState.isDiv3 ? 1 : 0;
            const columnList = this.state.noteData.columnList;
            columnList.splice(tuneState.columnIndex + 1, 0, {
                len4, len8, len16, len4div3, len8div3, soundIndex: -1
            });
            this.tune.setColumnList(columnList.slice());
        },

        remooveCur: () => {
            const tuneState = this.state.tuneState;
            const columnList = this.state.noteData.columnList;
            columnList.splice(tuneState.columnIndex, 1);
            this.tune.setColumnList(columnList.slice());
        },

        playSoundFont: (soundName: string, duration: number) => {
            const instrument = this.state.instrument;
            if (instrument != null) {
                instrument.stop();
                instrument.play(soundName, 0, { duration });
            }
        },

        adjustWidth: (isAdd: boolean) => {
            const tuneState = this.state.tuneState;
            const columnList = this.state.noteData.columnList;
            const column = { ...columnList[tuneState.columnIndex] };
            if (isAdd) {
                if (tuneState.usingLen === 4 && !tuneState.isDiv3) column.len4++;
                else if (tuneState.usingLen === 8 && !tuneState.isDiv3) column.len8++;
                else if (tuneState.usingLen === 16 && !tuneState.isDiv3) column.len16++;
                else if (tuneState.usingLen === 4 && tuneState.isDiv3) column.len4div3++;
                else if (tuneState.usingLen === 8 && tuneState.isDiv3) column.len8div3++;
            } else {
                if (tuneState.usingLen === 4 && !tuneState.isDiv3) column.len4--;
                else if (tuneState.usingLen === 8 && !tuneState.isDiv3) column.len8--;
                else if (tuneState.usingLen === 16 && !tuneState.isDiv3) column.len16--;
                else if (tuneState.usingLen === 4 && tuneState.isDiv3) column.len4div3--;
                else if (tuneState.usingLen === 8 && tuneState.isDiv3) column.len8div3--;
            }
            let total = column.len4 + column.len8 / 2 + column.len16 / 4;
            let totalDiv3 = column.len4div3 / 3 + column.len8div3 / 6;
            if (total + totalDiv3 > 0) {
                column.len4 = Math.floor(total);
                total -= column.len4;
                total *= 2;
                column.len8 = Math.floor(total);
                total -= column.len8;
                total *= 2;
                column.len16 = Math.floor(total);

                const subLen4 = Math.floor(totalDiv3);
                totalDiv3 -= subLen4;
                totalDiv3 *= 3;
                column.len4div3 = Math.floor(totalDiv3);
                totalDiv3 -= column.len4div3;
                totalDiv3 *= 2;
                const subLen8 = Math.floor(totalDiv3);
                totalDiv3 -= subLen8;
                totalDiv3 *= 3;
                column.len8div3 = Math.floor(totalDiv3);
                column.len4 += subLen4;
                column.len8 += subLen8;
                columnList[tuneState.columnIndex] = column;
                this.tune.setColumnList(columnList.slice());
            }
        },

        adjustScrollY: (columnIndex: number) => {

            const tuneState = this.state.tuneState;
            const columnList = this.state.noteData.columnList;
            const column = columnList[columnIndex];
            if (column.soundIndex !== -1) {
                const top = 22 * column.soundIndex - 350;
                tuneState.intervalRef.current.scrollTo({ top, behavior: 'smooth' });
                tuneState.tableRef.current.scrollTo({ top, behavior: 'smooth' });
            }
        },
        adjustScrollX: (columnIndex: number) => {
            const tuneState = this.state.tuneState;
            const pos = tuneState.posList[columnIndex];
            const left = pos * BEAT_WIDTH - 200;
            tuneState.tableRef.current.scrollTo({ left, behavior: 'smooth' });
        },

        playPreview: () => {

            const tuneState = this.state.tuneState;
            const columnList = this.state.noteData.columnList;
            const instrument = this.state.instrument as Soundfont.Player;
            const threads = this.state.noteState.backingThreads;
            const getSoundName = (soundIndex: number) => {

                const octave = Math.floor((87 + 9 - soundIndex) / 12);
                const keyIndex = (87 + 9 - soundIndex) % 12;
                const chordName = KEY12_SHARP_LIST[keyIndex];
                return chordName + octave;
            }

            let pastTime = 0;
            const bpm = this.state.noteState.cacheList[0].init.tempo;
            for (let i = tuneState.columnIndex; i < columnList.length; i++) {
                const column = columnList[i];

                const total = column.len4 + column.len8 / 2 + column.len16 / 4 + column.len4div3 / 3 + column.len8div3 / 6;
                const time = 60 / bpm * total;
                const soundName = getSoundName(column.soundIndex);
                threads.push(
                    setTimeout(() => {
                        tuneState.columnIndex = i;
                        tuneState.isEdit = true;
                        this.tune.setState(tuneState);
                        if (column.soundIndex !== -1) {
                            this.tune.playSoundFont(soundName, time);
                        }
                    }, pastTime * 1000)
                );
                pastTime += time;
            }
            threads.push(
                setTimeout(() => {
                    tuneState.isPreview = false;
                    this.tune.setState(tuneState);
                }, pastTime * 1000)
            );
        },

        stopPreview: () => {
            const threads = this.state.noteState.backingThreads;
            threads.forEach((thr) => {
                clearInterval(thr);
            });
            this.score.setNoteState({
                ...this.state.noteState,
                backingThreads: []
            });
        },

        moveAll: (value: number) => {
            const columnList = this.state.noteData.columnList;
            for (let i = 0; i < columnList.length; i++) {
                const column = columnList[i];
                if (column.soundIndex !== -1) {
                    column.soundIndex += value;
                }
            }
            this.tune.setColumnList(columnList.slice());
        }
    }
}